import Vue from "vue";
import Router from "vue-router";

Vue.use(Router);

const router = new Router({
  routes: [
    {
      path: "/",
      redirect: "/login",
    },
    {
      path: "/login",
      component: () =>
        import(/*webpackChunkName: 'login'*/ "../views/login/login.vue"),
    },
    {
      path: "/home",
      //   component: () =>
      //     import(
      //       /*webpackChunkName: 'home [hash]'*/ "../components/HelloWorld.vue"
      //     ),
      component: (resolve) =>
        require(["../components/HelloWorld.vue"], resolve),
    },
    {
      path: "/canvas",
      component: () =>
        import(/*webpackChunkName: 'canvas'*/ "../views/clock/clock.vue"),
    },
  ],
  mode: "history",
});

export default router;
